@charset "UTF-8";
*, *:before, *:after {
  box-sizing: border-box; }

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden; }

ul {
  margin: 0;
  padding: 0;
  list-style-type: none; }

a {
  -webkit-transition: all .2s linear;
  transition: all .2s linear;
  text-decoration: none; }
  a:hover {
    text-decoration: none; }

.lightgrey-gradient {
  background: #efefef;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#efefef), to(#eeeeee));
  background: linear-gradient(to bottom, #efefef 0%, #eeeeee 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#efefef', endColorstr='#eeeeee',GradientType=0 );
  /* IE6-9 */ }

html, body {
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  color: #000000; }

p {
  font-size: 1rem; }

small {
  font-size: 0.8rem;
  color: #919191; }

h1 {
  font-size: 3rem;
  color: #444444; }

h2 {
  font-size: 2.5rem;
  color: #444444; }

h3 {
  font-size: 2rem;
  color: #444444; }

h4 {
  font-size: 1.5rem;
  color: #444444; }

h5 {
  font-size: 1.2rem;
  color: #444444; }

h6 {
  font-size: 1rem;
  color: #444444; }

.card {
  background-color: #ffffff;
  border: 1px solid #eee;
  border-radius: 5px;
  height: 100%;
  box-shadow: 0px 1px 10px 1px rgba(0, 0, 0, 0.1);
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
  -webkit-box-pack: justify;
          justify-content: space-between; }
  .card .card-title {
    margin: 0;
    font-weight: lighter;
    font-size: 1.8rem; }
  .card .card-header {
    position: relative;
    padding: 1rem 1.5rem;
    border-bottom: 1px solid #eee; }
  .card .card-body {
    padding: 1rem 1.5rem;
    height: 100%; }
  .card .card-footer {
    padding: 1rem 1.5rem;
    border-top: 1px solid #eee; }

.notifications {
  top: 1.5rem !important;
  right: 1.5rem !important; }
  .notifications .vue-notification-template {
    padding: 0.8rem 1rem; }
  .notifications .notification-title {
    font-size: 1rem;
    margin-bottom: 0.4rem;
    font-weight: lighter; }
  .notifications .notification-content {
    font-size: 0.9rem; }

form .form-group {
  display: block;
  margin-bottom: 10px;
  margin-top: 10px; }
  form .form-group .error-message {
    color: #c40000;
    display: block; }
  form .form-group input {
    height: 32px; }
  form .form-group input, form .form-group textarea {
    padding: 6px;
    border-radius: 6px;
    border: 1px solid #ccc;
    font-size: 1rem;
    line-height: 1;
    color: #444444; }
    form .form-group input:not([type="checkbox"]):focus, form .form-group textarea:not([type="checkbox"]):focus {
      outline: none;
      box-shadow: 0 0 1px 1px cadetblue; }
    form .form-group input.form-control, form .form-group textarea.form-control {
      width: 100%; }
  form .form-group.checkbox-holder {
    position: relative; }
    form .form-group.checkbox-holder input + input {
      margin-left: 5px; }
    form .form-group.checkbox-holder input {
      position: relative;
      margin: 0;
      height: 32px;
      width: 32px; }
      form .form-group.checkbox-holder input:after {
        content: '';
        background-color: white;
        border: 1px solid #aaa;
        border-radius: 6px;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        -webkit-transition: all 0.02s cubic-bezier(0.075, 0.82, 0.165, 1);
        transition: all 0.02s cubic-bezier(0.075, 0.82, 0.165, 1); }
      form .form-group.checkbox-holder input:checked:after {
        font-size: 30px;
        font-family: monospace;
        content: 'â€¢';
        color: white;
        background-color: cadetblue;
        display: -webkit-box;
        display: flex;
        -webkit-box-pack: center;
                justify-content: center;
        -webkit-box-align: center;
                align-items: center; }
  form .form-group + .form-group {
    margin-top: 25px; }
  form .form-group label {
    display: block;
    font-weight: bolder;
    margin-bottom: 6px; }

form button[type="submit"] {
  border-color: transparent;
  border-radius: 6px;
  padding: 10px 16px;
  font-weight: lighter;
  color: white;
  font-size: 20px;
  background-color: cadetblue;
  box-shadow: 0px 2px 1px 0px rgba(39, 102, 104, 0.9); }
  form button[type="submit"]:hover {
    cursor: pointer; }
  form button[type="submit"]:focus {
    outline: 0; }
  form button[type="submit"]:active {
    outline: 0;
    box-shadow: 0px 1px 0.5px 0px rgba(39, 102, 104, 0.7); }
  form button[type="submit"].disabled {
    background-color: grey;
    box-shadow: 0px 2px 1px 0px rgba(0, 0, 0, 0.9); }

@media only screen and (max-width: 64em) {
  .container-fluid {
    padding-left: 1rem;
    padding-right: 1rem; } }
